.offer-promo {

  $block-name: &; // #{$block-name}__element {}
  width: 727px;
  margin: 0 auto;
  margin-right: 141px;

  &__title {
    font-size: 100px;
    color: $color-main;
    line-height: normal;
    margin-bottom: 20px;
  }

  &__text {
    padding-left: 83px;
    font-size: 27px;
    color: $color-main;
    margin-bottom: 31px;

    p {
      margin-bottom: 0;
      line-height: normal;
    }

    .danger {
      color: $color-danger;
    }
  }

  &__tab {
    padding: 25px 16px 27px 69px;
    box-shadow: -1px -1px 4px 0px rgba(0, 0, 0, 0.5);
    background-color: #fff;
    display: flex;
    position: relative;
    margin-bottom: 60px;

    &__img {
      width: 282px;
      margin-right: 21px;
      height: 361px;
      position: relative;
      background-color: #e0e0e2;

      &__block {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transition: all 200ms;
        transform: translate(0px, -100px);
        opacity: 0;

        &.show {
          transform: translate(0, 0);
          opacity: 1;
        }
      }
    }

    &__content {
      width: 338px;
    }
  }

  &__nav {
    position: absolute;
    top: -60px;
    left: -60px;
    pointer-events: none;

    &__num {
      display: block !important;
      //margin-bottom: 26px;
      margin-bottom: -30px;
      transition: none !important;
      height: 115px;

      &:last-child {
        margin-bottom: 0;
      }

      .circle {
        &__block {
          font-size: 43px;
          background-color: #D7D0D1;
          color: white;
        }

        &__wrapper {
          width: 52px;
          height: 52px;
          padding: 0;
          border-color: #D7D0D1;
        }
      }

      &.show {
        .circle {
          &__wrapper {
            width: 115px;
            height: 115px;
            padding: 16px;
            border-color: #015a51;
          }
          &__block {
            background-color: #015a51;
            font-size: 63px;
          }
        }
      }
    }
  }
}
