
// Grid breakpoints
//
// Define the minimum dimensions at which your layout will change,
// adapting to different screen sizes, for use in media queries.

$grid-breakpoints: (
        xs: 0,
        sm: 576px,
        md: 768px,
        lg: 992px,
        xl: 1200px
) !default;


// Grid containers
//
// Define the maximum width of `.container` for different screen sizes.

$container-max-widths: (
        sm: 540px,
        md: 720px,
        lg: 960px,
        xl: 1140px
) !default;


// Grid columns
//
// Set the number of columns and specify the width of the gutters.

$grid-columns:                12 !default;
$grid-gutter-width:           30px !default;


// Modals


///   https://colorscheme.ru/color-converter.html

$black:                       hsl(0, 0%, 0%);
$gray-darkest:                #333333;
$gray-darker:                 hsl(0, 0%, 20%);
$gray-darken:                 hsl(0, 0%, 30%);
$gray-dark:                   hsl(0, 0%, 40%);
$gray:                        #8891a6;
$gray-light:                  hsl(0, 0%, 60%);
$gray-lighten:                #c5cad9;
$gray-lighter:                hsl(0, 0%, 80%);
$gray-lightest:               darken(#e9edf2, 5%);
$gray-ultra-lightest:         #f3f5f9;
$white:                       hsl(0, 0%, 100%);

$color-main:                  #015a51;
$color-main--accent:          #af2f44;
$color-main--light:           #36aba3;
$color-main--ultralight:      #8dbab7;
$color-main--gold:            #c79b38;
$color-success:               #38a53d;
$color-success-2:             #00d092;
$color-success--light:        #ecf8ee;
$color-danger:                #dd6b72;
$color-danger--light:         #faedf1;
$color-warning:               #af2f44;



//  

$text-color:                  $gray-darkest;
$text-color--muted:           $gray;
$body-bg:                     #fff;

$link-color:                  $color-main;
$link-color--hover:           $color-main--accent;

//  

$font-size:                   14px;
$font-size--h1:               32px;
$font-size--h2:               24px;
$font-size--h3:               16px;
$font-size--h4:               14px;
$font-size--h5:               1em;
$font-size--h6:               1em;
$font-size--small:            12px;

$line-height:                 1.429em;

@font-face {
  font-family: 'clutchsans-regular';
  src: url('../fonts/clutchsans-regular.eot');
  src: local(''), url('../fonts/clutchsans-regular.woff') format('woff'), url('../fonts/clutchsans-regular.ttf') format('truetype'), url('../fonts/clutchsans-regular.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'akrobat-regular';
  src: url('../fonts/Akrobat-Regular.eot');
  src: local(''), url('../fonts/Akrobat-Regular.woff') format('woff'), url('../fonts/Akrobat-Regular.ttf') format('truetype'), url('../fonts/Akrobat-Regular.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}

$clutchsans-regular:          'clutchsans-regular';
$akrobat-regular:             'akrobat-regular';
$font-family:                 $clutchsans-regular;
$font-family--headings:       $font-family;



// 

$screen-xs:                   0;
$screen-sm:                   480px;
$screen-md:                   768px;
$screen-lg:                   1050px;
$screen-xl:                   1240px;
$screen-xxl:                  1600px;

$container-sm:                100%;
$container-md:                100%;
$container-lg:                100%;
$container-xl:                100%;
$container-xxl:               $screen-xxl - 30;

$breadcrumb-divider: quote(">");

.container {
  max-width: 1779px !important;

  @media(max-width: $screen-lg){
    padding-left: 20px;
    padding-right: 20px;
  }
}

.toLeftStart {
  transition: all 600ms;
  transform: translate(200px, 0);
  opacity: 0;
}
.toLeftEnd {
  transform: translate(0,0);
  opacity: 1;
}
.toRightStart {
  transition: all 600ms;
  transform: translate(-200px, 0);
  opacity: 0;
}
.toRightEnd {
  transform: translate(0,0);
  opacity: 1;
}


//  

$grid-columns:                24;
$grid-gutter-width:           30px;


// 

$field-padding-vertical:     9px;
$field-padding-horizontal:   14px;

$border-radius:               2px;
$border-color:                $gray-lightest;
$opacity:                     0.7;
$transition-time:             0.3s;
$shadow:                      0 4px 40px rgba(72, 72, 72, 0.35);

$z-index-modal:               100;

$header-height:               56px;

$menu-desktop-width:          $screen-xl;

$toggler-part-height:         2px;
$toggler-part-bg-color:       $gray-darkest;
